@use "sass:math";

@import "plugins/shareon.min";

@import "parts/reset";
@import "parts/variables";
@import "parts/mixins";
@import "parts/generators";
@import "parts/fonts";

$startRelative: 1580;

@function vwByPx($pxSize) {
  $percent: math.div($startRelative, 100);
  @return math.div($pxSize, $percent);
}

@mixin mediaRelative() {
  @media screen and (min-width: 768px) and (max-width: #{$startRelative}px) {
    @content
  }
}

html, body, *, :after, :before {
  outline: none !important;
  box-sizing: border-box;
}

body {
  background-color: #000103;

  &::-webkit-scrollbar {
    width: 0;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: transparent;
    border-radius: 0;
    border: 0 solid transparent;
  }
}

html {
  &.webp, &.no-webp {
    .greeting-card {
      display: block;
    }
  }
}

.share-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 100%;

  background-color: rgba(#000103, 0.9);
  transition: .5s;

  pointer-events: none;
  opacity: 0;

  &.is-on {
    pointer-events: auto;
    opacity: 1;
  }

  .share-block {
    max-width: 500px;
    width: 100%;
    height: auto;

    background-color: white;
    border-radius: 14px;

    padding: 24px;
    padding-top: 36px;
    position: relative;

    .share-block__close {
      display: flex;
      flex-shrink: 0;
      width: 18px;
      height: 18px;

      background-image: url('../images/icon-close.svg');
      background-position: center center;
      background-size: contain;
      background-repeat: no-repeat;

      position: absolute;
      top: 14px;
      right: 14px;
      cursor: pointer;
    }

  }


}

