
p {
  width: 100%;
}

.ie-special-picture {
  img {
    display: none !important;
    &.ie-picture {
      width: 100%;
      height: auto;
      display: block !important;
    }
  }
}

.container__clinical-case .clinical__section-menu .clinical__section-menu-item {
  max-width: 227px;
}

.container__clinical-case .clinical__sections-wrapper .clinical__section-item .section-content .section-content__radio-wrapper label .custom-radio::after {
  width: 8px;
  height: 8px;
}

.container__clinical-case span {
  width: 100%;
}

.container__clinical-case .clinical__sections-wrapper .clinical__section-item .section-content .section-content__radio-wrapper label {
  width: 100%;
}

.container__clinical-case .clinical__sections-wrapper .clinical__section-item .section-content .section-content__radio-wrapper {
  width: 100%;
}

.section-content__line-control-group {
  width: auto !important;
  .section-content__radio-wrapper {
    width: auto !important;
  }

  &.section-content__line-control-group-column {
    width: 100% !important;
    .section-content__radio-wrapper {
      width: 100% !important;
    }
  }

}

.clinical__popup-4, .clinical__popup-9 {
  .table-scheme {
    margin-top: 30px;
  }
}