@keyframes translateToRight {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-1000px);
  }
}

@keyframes jumpElf {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(15%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes translateYears {
  from {
    transform: translateX(3440px);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes animateSmile {
  0% {
    transform: translate(-5px, 5px) scale(0.2);
    opacity: 0.2;
  }
  25% {
    transform: translate(15px, -15px) scale(0.4);
    opacity: 1;
  }
  50% {
    transform: translate(-15px, -65px) scale(0.6);
    opacity: 1;
  }
  75% {
    transform: translate(15px, -105px) scale(0.8);
    opacity: 1;
  }
  100% {
    transform: translate(-10px, -155px) scale(1);
    opacity: 0;
  }
}

@keyframes animateSmile2 {
  0% {
    transform: translate(5px, 5px) scale(0.2);
    opacity: 0.2;
  }
  25% {
    transform: translate(-15px, -15px) scale(0.4);
    opacity: 1;
  }
  50% {
    transform: translate(15px, -65px) scale(0.6);
    opacity: 1;
  }
  75% {
    transform: translate(-15px, -105px) scale(0.8);
    opacity: 1;
  }
  100% {
    transform: translate(-15px, -155px) scale(1);
    opacity: 0;
  }
}

@keyframes animateSmile3 {
