  50% {
    transform: translate(-5px, -70px) scale(0.6);
    opacity: 1;
  }
  75% {
    transform: translate(-5px, -90px) scale(0.8);
    opacity: 1;
  }
  100% {
    transform: translate(-10px, -120px) scale(1);
    opacity: 0;
  }
}

@keyframes animateSmile4 {
  0% {
    transform: translate(10px, 5px) scale(0.2);
    opacity: 0.2;
  }
  25% {
    transform: translate(-17px, -28px) scale(0.4);
    opacity: 1;
  }
  50% {
    transform: translate(10px, -75px) scale(0.6);
    opacity: 1;
  }
  75% {
    transform: translate(-5px, -110px) scale(0.8);
    opacity: 1;
  }
  100% {
    transform: translate(8px, -140px) scale(1);
    opacity: 0;
  }
}

@keyframes hideBlockThroughOpacity {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes showBlockThroughOpacity {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

