  }
  to {
  }
}

@keyframes moveElfUptoDownFisher {
  0% {
    transform: translateX(-50%) translateY(0) rotate(0);
  }
  5% {
    transform: translateX(-50%) translateY(-45px) rotate(0);
  }
  25% {
    transform: translateX(-50%) translateY(-45px) rotate(0);
  }
  50% {
    transform: translateX(-50%) translateY(-45px) rotate(0);
  }
  55% {
    transform: translateX(-50%) translateY(-65px) rotate(0);
  }
  60% {
    transform: translateX(-50%) translateY(0) rotate(0);
  }
  100% {
    transform: translateX(-50%) translateY(0) rotate(0);
  }
}

@keyframes moveElfUptoDownChef {
  0% {
    transform: translateX(0) translateY(0) rotate(19deg);
  }
  5% {
    transform: translateX(0) translateY(-45px) rotate(19deg);
  }
  25% {
    transform: translateX(0) translateY(-45px) rotate(19deg);
  }
  50% {
    transform: translateX(0) translateY(-45px) rotate(19deg);
  }
  53% {
    transform: translateX(0) translateY(-55px) rotate(19deg);
  }
  60% {
    transform: translateX(0) translateY(0) rotate(19deg);
  }
  100% {
    transform: translateX(0) translateY(0) rotate(19deg);
  }
}

@keyframes moveElfUptoDownAstronaut {
  0% {
    transform: translateX(0) translateY(0) rotate(-5deg);
  }
  3% {
    transform: translateX(0) translateY(-50px) rotate(-5deg);
  }
  25% {
    transform: translateX(0) translateY(-50px) rotate(-5deg);
  }
  50% {
    transform: translateX(0) translateY(-50px) rotate(-5deg);
  }
  53% {
    transform: translateX(0) translateY(-60px) rotate(-5deg);
  }
  57% {
    transform: translateX(0) translateY(0) rotate(-5deg);
  }
  100% {
    transform: translateX(0) translateY(0) rotate(-5deg);
  }
}

@keyframes glow {
  0% {
    filter: drop-shadow(0 0 0 transparent);
  }
  50% {
    filter: drop-shadow(0 0 15px #FFF600);
  }
  100% {
    filter: drop-shadow(0 0 0 transparent);
  }
}

@keyframes moveToPath {
  100% {
    offset-distance: 100%;
  }
}

@keyframes animatedSnow {
  0% {
    transform: translateY(-60%);
  }
  100% {
    transform: translateY(0);
  }
}