@charset "UTF-8";
.shareon {
  font-size: 0 !important;
}

.shareon > * {
  display: inline-block;
  position: relative;
  height: 24px;
  min-width: 16px;
  margin: 3px;
  padding: 6px 10px;
  background-color: #333;
  border-radius: 3.33333px;
  border: 0;
  box-sizing: content-box;
  color: #fff;
  line-height: 1.5;
  transition: opacity 0.3s ease;
  vertical-align: middle;
}

.shareon > :hover {
  border: 0;
  cursor: pointer;
  opacity: 0.7;
}

.shareon > :not(:empty) {
  font-size: 16px;
  text-decoration: none;
}

.shareon > :not(:empty):before {
  position: relative;
  height: 100%;
  width: 28px;
  top: 0;
  left: 0;
  background-position: 0 50%;
}

.shareon > :before {
  display: inline-block;
  position: absolute;
  height: 20px;
  width: 20px;
  top: 8px;
  left: 8px;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  content: "";
  vertical-align: bottom;
}

